<template>
  <div class="login">
    <!-- 头部 -->
    <van-nav-bar
      title="会员登陆"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <!-- 登录自定义 -->
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input class="inp" v-model="picCode" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="this.picUrl" @click="getPic" alt="">
        </div>
        <div class="form-item">
          <input class="inp" v-model="smsCode" placeholder="请输入短信验证码" type="text">
          <button @click="getCaptcha">{{totalCount===second?'获取验证码':second+'后重新发送'}}</button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import { getPic, getMsgCode, codeLogin } from '@/api/login'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picUrl: '',
      picKey: '',
      totalCount: 30,
      second: 30,
      timer: null,
      picCode: '',
      mobile: '',
      smsCode: ''
    }
  },
  created () {
    this.getPic()
  },
  methods: {
    // 获得验证码图片
    async getPic () {
      const res = await getPic()
      const { data: { base64, key } } = res
      this.picUrl = base64
      this.picKey = key
    },
    // 验证手机号验证码格式
    verifyCode () {
      if (!/^1[0-9]\d{9}$/.test(this.mobile)) {
        this.$toast('手机号错误')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('验证码错误')
        return false
      }
      return true
    },
    // 获得短信验证码
    async getCaptcha () {
      if (!this.verifyCode()) {
        return
      }
      if (this.timer === null) {
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        // console.log(this.picCode)
        this.$toast('信息发送成功')
        this.timer = setInterval(() => {
          this.second--
          if (this.second === 0) {
            this.second = this.totalCount
            // 如果计时器不再需要，可以在此清理
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    },
    // 登录按钮
    async login () {
      if (!this.verifyCode()) {
        return
      }
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('短信验证码错误')
        return
      }
      const res = await codeLogin(this.mobile, this.smsCode)
      // 权证信息存入 vuex
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登陆成功')
      if (this.$route.query.url) {
        this.$router.replace(this.$route.query.url)
        return
      }
      this.$router.push('/')
      // console.log(res)
    }
  },
  // 离开页面清除定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
