export default {
  methods: {
    // 没登陆时 去登陆
    loginConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '需要先登录才能继续操作',
          confirmButtonText: '去登陆',
          cancelButtonText: '再看看'
        })
          .then(() => {
            // on confirm
            // 带查询参数，结果是 /register?plan=private
            this.$router.replace({
              path: '/login',
              query: { url: this.$route.fullPath }
            })
          })
          .catch(() => {
            // on cancel
          })
        return true
      }
      return false
    }
  }
}
