import { getCartList, changeCount, delSelect } from '@/api/cart'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCartList (state, newList) {
      state.cartList = newList
    },
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => { item.isChecked = !flag })
    },
    changeCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }
  },
  actions: {
    async getCartAction (context) {
      const { data: { list } } = await getCartList()
      list.forEach(element => {
        element.isChecked = true
      })
      console.log(list)
      context.commit('setCartList', list)
    },
    async changeCount (context, { goodsNum, goodsId, goodsSkuId }) {
      // 先更新视图
      context.commit('changeCount', { goodsId, goodsNum })
      // 再更新后端
      await changeCount(goodsId, goodsNum, goodsSkuId)
    },
    async delSelect (context) {
      const sel = context.getters.selCartList
      const selId = sel.map(item => item.id)
      const res = await delSelect(selId)
      console.log(res)
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 获得总商品数
    getTotal (state) {
      return state.cartList.reduce((sum, num) => sum + num.goods_num, 0)
    },

    // 获得选中的商品
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked === true)
    },

    // 获得选中商品数
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, num) => sum + num.goods_num, 0)
    },

    // 获得选中的总价
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, num) => sum + num.goods_num * num.goods.goods_price_min, 0).toFixed(2)
    },

    // 全选按钮的状态
    toggleCheckAll (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
