<template>
  <div class="count-box">
    <button @click="reduce" class="reduce">-</button>
    <input @change="change" type="text" :value="value" >
    <button @click="add" class="add">+</button>
  </div>
</template>

<script>
export default {
  methods: {
    reduce () {
      if (this.value === 1) return
      this.$emit('input', this.value - 1)
    },
    add () {
      this.$emit('input', this.value + 1)
    },
    change (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }
      this.$emit('input', num)
    }
  },
  props: {
    value: {
      type: Number,
      default: 1
    }
  }
}
</script>

<style lang="less">
  .count-box {
    display: flex;
    width: 110px;
    height: 30px;
    .reduce, .add {
      width: 30px;
      height: 30px;
      border: 0px solid #000;
      background-color: #efefef;
    }
    input {
      width: 40px;
      height: 30px;
      margin: 0 5px;
      outline: none;
      border: none;
      background-color: #efefef;
      text-align: center;
    }
  }
</style>
